.home  {

  &__header {
    h1 {
      font-size: 14vw;
      line-height: 12vw;
      margin-top: 1rem;
      margin-bottom: 0;
      font-weight: 500;
      letter-spacing: -8px;
      width: 100%;
      text-align: center;
    }

    h2 {
      font-weight: 400;
      padding: 0 1.8rem;
      text-align: justify;
      letter-spacing: -1px;
    }
  }

  &__projects {
    border: 1px solid black;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 0 1.8rem;
    border-radius: 7px;

    h3, p {
      margin: 0;
    }
    
    h3 {
      font-size: 23px;
      padding-left: 1rem;
      font-weight: 500;
    }

    p:last-child {
      padding-right: 1rem;
    }

    .remote {
      font-style: italic;
      p {
        padding: 0;
      }
    }

    .line {
      width: 1px;
      height: 33px;
      background-color: black;
    }
  }

  &__cards {
    padding: 1.8rem;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 2rem;

    div {
      border-radius: 10px;
      height: 150px;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    h3 {
      font-weight: 400;
      font-size: 32px;
    }

    // sltwtr
    .mother {
      background: #efda9a;
      color: white;
    }
    .caddis {
      background: #F48029;
      color: white;
    }
    .volcom {
      background: black;
      color: white;
    }

    // gradiweb
    .bleen {
      background-color: #4FF49E;
      color: #3127F0;
    }
    .eovolt {
      background-color: #103C4D;
      color: white;
    }
    .luneale {
      background: #D2815F;
      color: white;
    }

    // unusualminds
    .artify {
      background-color: #FEBC5F;
      color: white;
    }
    .iconshock {
      background-color: #F35C76;
      color: white;
    }
    .bypeople {
      background: #9B01E5;
      color: white;
    }

    // wordspeakers
    // .word-spekaers {

    // }

    // .kao-wok {

    // } 

    .roxanne {
      background: black;
      color: white;
    }

    .piedemonte {
      background: rgb(48, 124, 160);
      color: white;
    }

    .visionchamanica {
      background: rgb(225, 215, 104);
      color: black;
    }

  }

  &__tags {

    padding: 0 1.8rem;
    display: flex;
    justify-content: center;
    margin-bottom: 2rem;
    gap: .5rem;

    &--item {
      border: 1px solid black;
      border-radius: 100px;
      width: fit-content;
      padding: 5px 20px;
      p {
        margin:0;
      }
    }
  }


}
